import deepExtend from 'deep-extend';
import Log from '../utils/Log';
import AxiosConfig from './AxiosConfig';

let ueboot_config = {
    sysTitle: 'ueboot',
    logoImage: '/static/img/logo.png',
    page_login: {
        //  ['theme1','theme2']
        theme: 'theme2',
        //
        successRouter:{path:'/'},
        //logo
        logoStyle:'width:100px;height:100px;',
        bgImage:'@/asserts/graphic3.svg',
        formTitle:''
    },
    page_main: {
        //key,ueboot_login_info
        userNameKey:'username',
        menuWidth:'200px',
        logoStyle: {
            minWidth: '250px'
        },
        //
        rightTheme:'dropdown',
        rightStyle: {
            minWidth: '100px'
        },
        dropdown:{
            //itemitemitemitem
            //name
            items:[
                {name:'',disabled:false,divided:false,icon:'md-create'},
                {name:'',disabled:false,divided:false,icon:'md-exit'}
            ],
            //iconsrc
            avatar:{
                icon:'ios-person',
                src:'',
                style:null
            }
           
        },
        //
        logoutSuccessRouter:{name:'Login'}
    },
    axios: {
        baseURL:'',
        unauthorizedUrl:'/#/login'
    },
    //this.$log.d()
    log:{
        level:3,
    }
};


/**
 * Main
 */
export default {
    setConfig(conf) {
        ueboot_config = deepExtend({},ueboot_config, conf );
        //log
        Log.config(ueboot_config.log);
        //axios
        AxiosConfig.init(ueboot_config.axios);

    },
    getConfig() {
        return ueboot_config;
    }

};



