/**
 * Created by yangkui on 17/7/5.
 * formGrid
 */
export default {
    options: {
        autoLoad: true,
        url: {
            save: '', // id;
            delete: '', // id;
            page: '', // ;
            get: '', // keys;
            im: '/admin/api/dic/find/parent' // List,name,value
        }
    },
    tips: {'title': '', 'content': ''},
    toolbar: {
    // 
        show: true,
    // startend
        justify: 'start',
        refresh: {
            show: true, label: ''
        },
        create: {
            show: true, label: ''
        },
        delete: {
            show: true, label: '', loading: false
        },
        groups: {
            show: false,
            label: '/',
            import: {
                show: false, size: 50 * 1024, modelTitle: '', templatePath: '', params: {}
            }
        },
    // 
        buttons: null,
    // 
        filter: {
            show: false,
            name: 'keyWord',
            placeholder: ''
        },
    // columnsitemsformcolumns
        superFilter: {
            show: false,
            label: '',
            labelWidth: 80,
            name: 'searchForm',
            colNumber: 4,
            columns: [],
      // columns
            rows: {},
            submit: {theme: 'primary', label: '', icon: 'md-search', long: false},
            reset: {theme: 'primary', label: '', icon: 'md-close', long: false, ghost: true}
        }
    },
    pageable: {
        page: 1,
        size: 15
    },
  // 
    form: {
        name: 'formName', // 
    // label
        labelPosition: 'right',
        loading: false, // loading
    // label
        labelWidth: 80,
    // 
        colNumber: 2,
        modal: {
            title: '', // .;
            showClose: true, // true;
            width: '' // 
        },

        columns: [],
    // false,
        submitBefore: function (data) {
            return true;
        },

    // 
        submitAfter: function (response) {
            return true;
        },

    // 
        onCancel: function () {

        },
        data: {}// 
    },
    table: {
        height: '', // 100%
    // 
        showCheckbox: true,
    // 
        selection: {
            type: 'selection',
            width: 60,
            align: 'center'
        },
        stripe: true,
        noDataText: '',
        tableLoadingText:',...',
        tableLoadedErrorText:'',
    // 
        operation: {
      // id
            keys: ['id'],
      // 
            show: true,
      // false
            remote: false,
      // 
            buttons: [{
                key: 'view',
                show: true,
                'label': '',
                'theme': 'primary',
                click: (row, index, _this) => {
                    _this.optViewClick(row, index);
                }
            }, {
                key: 'edit',
                show: true,
                'label': '',
                'theme': 'primary',
                click: (row, index, _this) => {
                    _this.optEditClick(row, index);
                }
            }, {
                key: 'delete',
                show: true,
                'label': '',
                'theme': 'primary',
                ghost: true,
                click: (row, index, _this) => {
                    _this.optDeleteClick(row, index);
                }
            }],
      // 
            column: {
                title: '',
                key: 'action',
                className: 'optColumn',
                align: 'center',
                minWidth: 160
            }
        },
    // 
        data: []
    },
    queryParams: {}

};



