<template>
  <div>
    <u-form-grid :data="formGrid" ref="formGrid"></u-form-grid>
    <Modal v-model="permissionModal" size="large">
      <p slot="header">
        <span style="color:red; vertical-align: top;">{{selectRoleName}}</span>
      </p>
      <div>
        <u-tree v-model="selectTreeItem" :tree="tree" :showCheckbox="treeOptions.showCheckbox"
                :multiple="treeOptions.multiple"
                :allow-batch="treeOptions.allowBatch"
                ref="utree" v-show="!loadingTree"></u-tree>
        <Spin v-if="loadingTree">
          <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
          <div></div>
        </Spin>
      </div>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit()" :loading="loading"></Button>
        <Button @click="()=>{this.permissionModal = false;}" style="margin-left: 8px" :disabled="loading"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
    name: 'Role',
    data () {
        return {
            permissionModal: false,
            selectRoleName: '',
            selectRoleId: null,
      // 
            selectTreeItem: null,
            loadingTree: false,
            loading: false,
      // 
            tree: [],
            treeOptions: {
                showCheckbox: true,
                multiple: true,
                allowBatch: true
            },
            formGrid: {
                options: {
                    url: {
                        page: '/ueboot/role/page',
                        save: '/ueboot/role/save',
                        delete: '/ueboot/role/delete'
                    }
                },
                toolbar: {
                    superFilter: {
                        columns: [{type: 'hidden', name: 'roleId', init: null}]
                    }
                },
                form: {
                    modal: {
                        title: ''
                    },
                    columns: [
            {label: '', type: 'text', name: 'name', required: true},
            {label: '', type: 'text', name: 'description'},
                        {
                            label: '',
                            type: 'select',
                            name: 'available',
                            required: true,
                            data: [{'name': '', 'value': 'true'}, {'name': '', 'value': 'false'}],
                            init: 'true'
                        }
                    ]
                },
                table: {
                    operation: {
                        primaryKey: 'id',
                        buttons: [
                            {
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                click: (row, index, _this) => {
                                    this.permissionModal = true;
                                    this.selectRoleName = row['name'];
                                    this.selectRoleId = row['id'];
                                    this.fetchTreeData();
                                }
                            }, {
                                key: 'edit',
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                click: (row, index, _this) => {
                                    _this.optEditClick(row, index);
                                }
                            }, {
                                key: 'delete',
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                ghost: true,
                                click: (row, index, _this) => {
                                    _this.optDeleteClick(row, index);
                                }
                            }
                        ]
                    },
                    columns: [
            {title: 'id', key: 'id', minWidth: 60},
            {title: '', key: 'name', minWidth: 160},
            {title: '', key: 'description', minWidth: 100},
                        {
                            title: '',
                            key: 'available',
                            minWidth: 120,
                            fieldFormat: (value, row) => {
                                if (value) {
                                    return '';
                                } else {
                                    return {value: '', cellClassName: 'table-cell-red'};
                                }
                            }
                        }
                    ]
                }
            }
        };
    },
    methods: {
        fetchTreeData () {
            let newTree = [];
            this.loadingTree = true;
            let p1 = new Promise((resolve, reject) => {
                if (this.tree.length !== 0) {
                    newTree = [...this.tree];
                    newTree.forEach((t) => {
                        t.selected = false;
                    });
                    resolve();
                    return;
                }
                this.$axios.post('/ueboot/resources/list', {}).then((response) => {
                    if (!response.body) {
                        reject(response);
                        return;
                    }
                    response.body.forEach((t) => {
                        if (t.resourceType === '') {
                            t.icon = 'fa fa-file';
                        }
                    });
                    newTree = response.body;
                    resolve();
                });
            });
            p1.then(() => {
                this.$axios.post('/ueboot/permission/findByRoleId', {roleId: this.selectRoleId}).then((response) => {
                    this.loadingTree = false;
                    if (response.body && response.body.length > 0) {
                        let resourceIds = [];
                        response.body.forEach((t) => {
                            return resourceIds.push(t.resourceId);
                        });
            // 
                        newTree.forEach((t) => {
                            if (resourceIds.includes(t.id)) {
                                t.selected = true;
                            }
                        });
                    }
                    this.tree = newTree;
                });
            });
        },
        handleSubmit () {
            this.loading = true;
            let resourceIds = this.$refs['utree'].getCheckedNodes();
            this.$axios.post('/ueboot/permission/save', {
                roleId: this.selectRoleId,
                resourceIds: resourceIds
            }).then((response) => {
                this.$Message.success('');
                this.loading = false;
            });
        }
    }
};
</script>



