<template>
  <div>
    <u-form-grid :data="formGrid" ref="formGrid"></u-form-grid>
    <Modal v-model="resetPwdModal">
      <p slot="header">
        <span style="color:red; vertical-align: top;">{{formCustom.userName}}</span> 
      </p>
      <Form ref="formCustom" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <FormItem label="" prop="password" required>
          <i-input type="password" v-model="formCustom.password"></i-input>
        </FormItem>
        <FormItem label="" prop="passwdCheck">
          <i-input type="password" v-model="formCustom.passwdCheck"></i-input>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit('formCustom')" :loading="loading"></Button>
        <Button @click="()=>{this.resetPwdModal = false}" style="margin-left: 8px" :disabled="loading"></Button>
      </div>
    </Modal>
    <Modal v-model="setRoleModal" size="large">
      <p slot="header">
        <span style="color:red; vertical-align: top;">{{formCustom.userName}}</span> 
      </p>
      <Form ref="formCustom2" :model="formCustom" :rules="ruleCustom" :label-width="80">
        <FormItem label="" prop="roles" required>
          <Select v-model="formCustom.roleIds" multiple style="width:260px">
            <Option v-for="item in roleData" :value="item.value" :key="item.value">{{ item.name }}</Option>
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button type="primary" @click="handleSubmit('formCustom2')" :loading="loading"></Button>
        <Button @click="()=>{this.setRoleModal = false;this.formCustom.roles = ''}" style="margin-left: 8px"
                :disabled="loading">
        </Button>
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
    name: 'User',
    data () {
        const validatePassCheck = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.formCustom.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            loading: false,
            setRoleModal: false,
            roleData: [],
            resetPwdModal: false,
            formCustom: {
                id: null,
                userId: null,
                userName: '',
                password: '',
                passwdCheck: '',
                roleIds: ''
            },
            ruleCustom: {
                password: [{required: true, message: '', trigger: 'blur', type: 'string'}],
                passwdCheck: [{type: 'string', trigger: 'blur', required: true},
          {validator: validatePassCheck, trigger: 'blur'}
                ]
            },
            formGrid: {
                options: {
                    url: {
                        page: '/ueboot/user/page',
                        save: '/ueboot/user/save',
                        delete: '/ueboot/user/delete'
                    }
                },
                form: {
                    labelWidth: 120,
                    modal: {
                        title: ''
                    },
                    columns: [
            {label: '', type: 'text', name: 'userName', required: true, edit: {disabled: true}},
                        {
                            label: '',
                            type: 'password',
                            name: 'password',
                            required: true,
                            view: {show: false},
                            edit: {show: false}
                        },
                        {
                            label: '',
                            type: 'password',
                            name: 'password2',
                            required: true,
                            view: {show: false},
                            edit: {show: false},
                            equalsTo: 'password'
                        },

                        {
                            label: '',
                            type: 'select',
                            name: 'locked',
                            data: [{'name': '', 'value': 'true'}, {'name': '', 'value': 'false'}],
                            init: 'false'
                        },
            {label: '', type: 'datetime', name: 'credentialExpiredDate'}

                    ]
                },
                table: {
                    operation: {
                        primaryKey: 'id',
                        buttons: [
                            {
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                click: (row, index, _this) => {
                                    if (this.roleData.length === 0) {
                                        this.fetchRoleData();
                                    }
                                    this.setRoleModal = true;
                                    this.formCustom.userId = row['id'];
                                    let roleIds = row['roleIds'] || '';
                                    if (roleIds.includes(',')) {
                                        let a = [];
                                        roleIds.split(',').forEach((r) => {
                                            if (r !== '') {
                                              a.push(parseInt(r));
                                          }
                                        });
                                        this.formCustom.roleIds = a;
                                    }
                                    this.formCustom.userName = row['userName'];
                                }
                            }, {
                                key: 'edit',
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                click: (row, index, _this) => {
                                    _this.optEditClick(row, index);
                                }
                            }, {
                                key: 'delete',
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                ghost: true,
                                click: (row, index, _this) => {
                                    _this.optDeleteClick(row, index);
                                }
                            }, {
                                show: true,
                                'label': '',
                                'theme': 'primary',
                                click: (row, index, _this) => {
                  // 
                                  this.resetPwdModal = true;
                                  this.formCustom.id = row['id'];
                                  this.formCustom.userName = row['userName'];
                              }
                            }
                        ],
                        column: {
                            minWidth: 220
                        }
                    },
                    columns: [
            {title: 'id', key: 'id', minWidth: 40, align: 'center'},
            {title: '', key: 'userName', minWidth: 80},
            {title: '', key: 'roleNames', minWidth: 120},
                        {
                            title: '',
                            key: 'locked',
                            minWidth: 80,
                            align: 'center',
                            fieldFormat: (value, row) => {
                                if (value) {
                                    return {value: '', cellClassName: 'table-cell-red'};
                                } else {
                                    return '';
                                }
                            }
                        },
            {title: '', key: 'credentialExpiredDate', minWidth: 120, align: 'center'}
                    ]
                }
            }
        };
    },
    methods: {
    // 
        fetchRoleData () {
            this.$axios.post('/ueboot/role/list', {}).then((response) => {
                let tree = [];
                response.body.forEach((t) => {
                    let t1 = {'name': t.name, 'value': t.id};
                    tree.push(t1);
                });
                this.roleData = tree;
            });
        },
        handleSubmit (name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    this.loading = true;
                    if (name === 'formCustom2') {
                        this.$axios.post('/ueboot/userRole/save', this.formCustom).then((response) => {
                            this.loading = false;
                            this.setRoleModal = false;
                            this.formCustom.roleIds = null;
                            this.$Message.success('');
              // 
                            this.$refs.formGrid.$emit('refreshData');
                        }).catch(() => {
                            this.loading = false;
                        });
                    } else {
                        this.$axios.post('/ueboot/user/save', this.formCustom).then((response) => {
                            this.loading = false;
                            this.resetPwdModal = false;
                            this.$Message.success('');
                        }).catch(() => {
                            this.loading = false;
                        });
                    }
                } else {
                    this.$Message.error('');
                }
            });
        },
        handleReset (name) {
            this.$refs[name].resetFields();
        }
    }
};
</script>



