<template>
  <div>
    <Row>
      <i-col :span="6">
        <Row>
          <Button icon="md-refresh" type="primary" @click="fetchTreeData"></Button>
        </Row>
        <Row style="margin-top:20px">
          <u-tree :tree="tree" @item-click="itemClick"></u-tree>
          <Spin v-if="loadingTree">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div></div>
          </Spin>
        </Row>
      </i-col>
      <i-col :span="18">
        <u-form-grid :data="formGrid" ref="formGrid"></u-form-grid>
      </i-col>
    </Row>
  </div>
</template>

<script>
import deepExtend from 'deep-extend';

export default {
    name: 'Resources',
    data () {
        return {
            loadingTree: false,
            tree: [],
            formGrid: {
                options: {
                    url: {
                        page: '/ueboot/resources/page',
                        save: '/ueboot/resources/save',
                        delete: '/ueboot/resources/delete'
                    }
                },
                toolbar: {
                    superFilter: {
                        columns: [{type: 'hidden', name: 'parentId'}]
                    }
                },
                form: {
                    labelWidth: 100,
                    modal: {
                        title: {
                            add:'',
                            edit:'',
                            view:''
                        }
                    },
                    columns: [
                        {
                            label: '',
                            type: 'select',
                            name: 'resourceType',
                            required: true,
                            data: [{'name': '', 'value': ''}, {'name': '', 'value': ''},
                {'name': '', 'value': ''}
                            ],
                            onChange: (value) => {
                                console.log(value);
                                this.changeResourceType(value);
                            }
                        },
            {label: '', type: 'text', name: 'name', required: true},
                        {label: '', type: 'treeSelect', name: 'parentId', required: true, tree: [],fieldFormat:(value,row)=>{
                //parentId
                            if(!value){
                                return 0;
                            }
                            return value;
                        }},
                        {
                            label: 'icon',
                            type: 'text',
                            placeholder: 'icon',
                            name: 'iconName',
                            fieldFormat: (value, row) => {
                                let themeJson = row['themeJson'] || null;
                                if (themeJson !== null) {
                                    return JSON.parse(themeJson)['iconName'];
                                } else {
                                    return '';
                                }
                            }
                        },
                        {
                            label: '',
                            type: 'text',
                            placeholder: ':#FFFFFF',
                            name: 'fontColor',
                            fieldFormat: (value, row) => {
                                let themeJson = row['themeJson'] || null;
                                if (themeJson !== null) {
                                    return JSON.parse(themeJson)['fontColor'];
                                } else {
                                    return '';
                                }
                            }
                        },
            {label: '', type: 'text', name: 'url', required: true},
            {label: '', type: 'text', name: 'permission', required: true},
            {label: '', type: 'number', placeholder: '', name: 'rank'},
                        {
                            label: '',
                            type: 'select',
                            name: 'available',
                            data: [{'name': '', 'value': 'true'}, {'name': '', 'value': 'false'}
                            ],
                            init: 'true'
                        }
                    ],
          // false,
          // data
                    submitBefore: function (data) {
            // 
                        data['theme'] = {'iconName': data['iconName'], 'fontColor': data['fontColor']};
                        return true;
                    },
                    submitAfter: () => {
            // 
                        this.fetchTreeData();
                    }
                },
                table: {
                    size: 'small',
                    operation: {
                        primaryKey: 'id'
                    },
                    columns: [
            {title: 'id', key: 'id', minWidth: 60},
            {title: '', key: 'name', minWidth: 100},
            {title: '', key: 'resourceType', minWidth: 100},
            {title: '', key: 'url', minWidth: 150},
                        {
                            title: 'icon',
                            key: 'iconName',
                            minWidth: 100,
                            fieldFormat: (value, row) => {
                                let themeJson = row['themeJson'] || null;
                                if (themeJson !== null) {
                                    return JSON.parse(themeJson)['iconName'];
                                } else {
                                    return '';
                                }
                            }
                        },
                        {
                            title: '',
                            key: 'fontColor',
                            minWidth: 100,
                            fieldFormat: (value, row) => {
                                let themeJson = row['themeJson'] || null;
                                if (themeJson !== null) {
                                    return JSON.parse(themeJson)['fontColor'];
                                } else {
                                    return '';
                                }
                            }
                        },
            {title: '', key: 'permission', minWidth: 140},
            {title: '', key: 'parentName', minWidth: 100},
            {title: '', key: 'rank', minWidth: 80, init: 1},
                        {
                            title: '',
                            key: 'available',
                            align: 'center',
                            minWidth: 120,
                            fieldFormat: (value, row) => {
                                if (value) {
                                    return '';
                                } else {
                                    return {value: '', cellClassName: 'table-cell-red'};
                                }
                            }
                        }
                    ]
                }
            }
        };
    },
    created () {
        this.fetchTreeData();
    },
    methods: {
    // 
        fetchTreeData () {
            this.loadingTree = true;
            this.$axios.post('/ueboot/resources/list', {}).then((response) => {
                this.loadingTree = false;
        // 
                let tree = [{id: 0, 'name': '', parentId: null}];
                response.body.forEach((t) => {
                    if (t.resourceType === '') {
                        t.icon = 'fa fa-file';
                    }
                });
                this.tree = [...response.body];
        // 
                let formTree = [...tree, ...response.body];
        // treetreeformGrid
                let newTree = [];
                formTree.forEach((t) => {
                    let n = deepExtend({}, t);
                    newTree.push(n);
                });
        // formtree
                this.$set(this.formGrid.form.columns[2], 'tree', newTree);
            });
        },
        changeResourceType (value) {
            if (value === '') {
        // 
                this.formGrid.form.columns.forEach((c) => {
                    if (['url', 'permission'].includes(c.name)) {
                        c.add = {show: false};
                        c.edit = {show: false};
                        c.view = {show: false};
                        c.required = false;
                    }
                });
            } else if (value === '') {
        // 
                this.formGrid.form.columns.forEach((c) => {
                    if (['url', 'iconName', 'fontColor', 'rank'].includes(c.name)) {
                        c.add = {show: false};
                        c.edit = {show: false};
                        c.view = {show: false};
                        c.required = false;
                    }
                });
            } else {
                this.formGrid.form.columns.forEach((c) => {
                    if (['url', 'permission', 'iconName', 'fontColor', 'rank'].includes(c.name)) {
                        c.add = {show: true};
                        c.edit = {show: true};
                        c.view = {show: true};
                        c.required = true;
                    }
                    if (['fontColor', 'iconName'].includes(c.name)) {
                        c.required = false;
                    }
                });
            }
        },
        itemClick (oriNode, oriItem, e) {
      // grid.
            this.$set(this.formGrid.toolbar.superFilter.columns[0], 'init', oriItem.id);
            this.$set(this.formGrid.form.columns[2], 'init', oriItem.id);

            this.$refs.formGrid.$emit('reloadData');
        }
    }
};
</script>
<style>
  .table-cell-green {
    background-color: #0c6 !important;
    color: #fff;
  }

  .table-cell-red {
    background-color: red !important;
    color: #fff;
  }
</style>



